import React, { Component } from 'react'
import ProductManagementData from './product-management-data'
import { withSetting } from '../../../shared/context';

class ProductManagement extends Component {
    render() {
        let userId = this.props.context.auth.user && this.props.context.auth.user.uid;
        return (
            userId && <ProductManagementData userId={userId} />
        )
    }
}

export default withSetting(ProductManagement)