import React, { Component } from "react";
import history from "../../../config/history";
import { ROUTES_CONSTANTS } from "../../../config/routes-constants";
import ProductManagementForm from "./product-management-form";
import { withSetting } from "../../../shared/context";
import moment from "moment";

class ProductManagementEdit extends Component {
  state = {
    productLines: [],
    brands: [],
    product: null
  };

  saveEntity = item => {
    const { firebase } = this.props.context;

    let uid = this.props.match.params.id;

    item.created = !item.created ? moment.now() : item.created;
    item.updated = moment.now();

    firebase.product(uid).set(item, error => {
      if (error) {
        console.log(error);
      } else {
        history.push(ROUTES_CONSTANTS.PRODUCT.index());
      }
    });
  };

  async componentDidMount() {
    const product = await this.getProduct();
    const productLines = await this.getProductLines();
    const brands = await this.getBrands();

    this.setState({ productLines, brands, product });
  }

  getProduct = () => {
    return new Promise((resolve, reject) => {
      let uid = this.props.match.params.id;
      this.props.context.firebase
        .product(uid)
        .once("value")
        .then(snapshot => {
          const item = snapshot.val();
          resolve(item);
        })
        .catch(error => {
          console.log(error);
          reject(error);
        });
    });
  };

  getProductLines = () => {
    return new Promise((resolve, reject) => {
      this.props.context.firebase.productLines().on(
        "value",
        snapshot => {
          const itemsObject = snapshot.val();
          const itemsList = itemsObject
            ? Object.keys(itemsObject).map(key => ({
                ...itemsObject[key],
                uid: key
              }))
            : [];
          // console.log(itemsList);
          resolve(itemsList);
        },
        error => {
          console.log(error);
          reject(error);
        }
      );
    });
  };

  getBrands = () => {
    return new Promise((resolve, reject) => {
      this.props.context.firebase.brands().on(
        "value",
        snapshot => {
          const itemsObject = snapshot.val();
          const itemsList = itemsObject
            ? Object.keys(itemsObject).map(key => ({
                ...itemsObject[key],
                uid: key
              }))
            : [];
          // console.log(itemsList);
          resolve(itemsList);
        },
        error => {
          console.log(error);
          reject(error);
        }
      );
    });
  };

  render() {
    return (
      this.state.product && (
        <ProductManagementForm
          title={"Actualizar Producto"}
          saveEntity={this.saveEntity}
          item={this.state.product}
          productLines={this.state.productLines}
          brands={this.state.brands}
        />
      )
    );
  }
}

export default withSetting(ProductManagementEdit);
