import React, { Component } from "react";
import {
  Container,
  Table,
  Button,
  Row,
  ButtonGroup
} from "reactstrap";
import { handleLink } from "../../../shared/util/link-utils";
import { ROUTES_CONSTANTS } from "../../../config/routes-constants";
import Loading from "../../layouts/loading";
import { withSetting } from "../../../shared/context";
import '../../../index.css'

class BrandManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      items: {}
    };

    this.getItems = this.props.context.firebase.companies;
  }

  componentDidMount() {
    this.setState({ loading: true });
    this.getItems().on("value", snapshot => {
      const itemsObject = snapshot.val();
      const itemsList = itemsObject
        ? Object.keys(itemsObject).map(key => ({
            ...itemsObject[key],
            uid: key
          }))
        : [];
      this.setState({
        items: itemsList,
        loading: false
      });
    });
  }

  componentWillUnmount() {
    this.getItems().off();
  }

  render() {
    const { items, loading } = this.state;
    return (
      <Container>
        <h2 className="pt-5">Compa&ntilde;ias</h2>
        {loading && <Loading />}
        <Row className="justify-content-end pb-1">
            <Button className="btn-created"
              onClick={e => handleLink(e, ROUTES_CONSTANTS.COMPANY.create())}
            >
              Crear
            </Button>
        </Row>
        <div className="table-responsive">
            <Table striped bordered hover>
              <thead className="table-head">
                <tr>
                  <th>#</th>
                  <th>Nombre</th>
                  <th>Descripci&oacute;n</th>
                  <th>Acciones</th>
                </tr>
              </thead>
              <tbody>
                {items.length > 0 &&
                  items.map((item, i) => (
                    <tr key={item.uid}>
                      <td>{i + 1}</td>
                      <td>{item.name}</td>
                      <td>{item.description}</td>
                      <td className="row-table-actions text-center">
                        <ButtonGroup aria-label="Basic example">
                          <Button
                            className="btn-view"
                            onClick={e =>
                              handleLink(e, ROUTES_CONSTANTS.COMPANY.view(item.uid))
                            }
                          >
                            View
                          </Button>
                          <Button
                            className="btn-edit"
                            onClick={e =>
                              handleLink(e, ROUTES_CONSTANTS.COMPANY.edit(item.uid))
                            }
                          >
                            Edit
                          </Button>
                          <Button
                            className="btn-delete"
                            onClick={e =>
                              handleLink(
                                e,
                                ROUTES_CONSTANTS.COMPANY.delete(item.uid)
                              )
                            }
                          >
                            Delete
                          </Button>
                        </ButtonGroup>
                      </td>
                    </tr>
                  ))}
              </tbody>
            </Table>
        </div>
      </Container>
    );
  }
}

export default withSetting(BrandManagement);
