import app from "firebase/app";
import "firebase/auth";
import "firebase/database";
import { DB_CONSTANTS } from "./db-constants";

const fbConfig = {
  apiKey: "AIzaSyDInxcKjFND6CPzzrqfpDUJwfBgFHAPJJU",
  authDomain: "writter-app-management.firebaseapp.com",
  databaseURL: "https://writter-app-management.firebaseio.com",
  projectId: "writter-app-management",
  storageBucket: "writter-app-management.appspot.com",
  messagingSenderId: "904791426685",
  appId: "1:904791426685:web:45c2edf19ecf22bca01314",
  measurementId: "G-WR58CXQRLF"
};
class Firebase {
  constructor() {
    app.initializeApp(fbConfig);

    this.auth = app.auth();
    this.db = app.database();
    this.googleProvider = new app.auth.GoogleAuthProvider();
  }

  // *** Auth API ***
  loginWithGoogle = () => this.auth.signInWithPopup(this.googleProvider);
  logout = () => this.auth.signOut();

  // *** User API ***
  users = () => this.db.ref(DB_CONSTANTS.USER.getAll());
  user = uid => this.db.ref(DB_CONSTANTS.USER.getOne(uid));

  // *** Company API ***
  companies = () => this.db.ref(DB_CONSTANTS.COMPANY.getAll());
  company = uid => this.db.ref(DB_CONSTANTS.COMPANY.getOne(uid));

  // *** Product Lines API ***
  productLines = () => this.db.ref(DB_CONSTANTS.PRODUCT_LINE.getAll());
  productLine = uid => this.db.ref(DB_CONSTANTS.PRODUCT_LINE.getOne(uid));

  // *** Brand API ***
  brands = () => this.db.ref(DB_CONSTANTS.BRAND.getAll());
  brand = uid => this.db.ref(DB_CONSTANTS.BRAND.getOne(uid));

  // *** Product API ***
  products = () => this.db.ref(DB_CONSTANTS.PRODUCT.getAll());
  product = uid => this.db.ref(DB_CONSTANTS.PRODUCT.getOne(uid));
}
export default Firebase;
