import React, { Component } from "react";
import {
  Button,
  Card,
  CardImg,
  CardBody,
  CardTitle,
  Container,
  Row,
  Alert
} from "reactstrap";

import logo from "../../../logo.svg";
import { isAdmin, ROLES } from "../../../shared/session/auth-data";
import history from "../../../config/history";
import { ROUTES_CONSTANTS } from "../../../config/routes-constants";
import { withSetting } from "../../../shared/context";

class LoginPage extends Component {
  state = {
    error: null,
  }

  loginWithGoogle = () => {
    this.props.context.firebase
      .loginWithGoogle()
      .then(socialAuthUser => {
        const userTemp = {
          username: socialAuthUser.user.displayName,
          email: socialAuthUser.user.email,
          roles: isAdmin(socialAuthUser.user.email)
            ? ROLES.ADMIN
            : ROLES.WRITTER
        };
        this.props.context.auth.setUser(userTemp);
        return this.props.context.firebase
          .user(socialAuthUser.user.uid)
          .set(userTemp);
      })
      .then(() => {
        history.push(ROUTES_CONSTANTS.HOME);
      })
      .catch(error => {
        console.log(error);
        
      });
  };

  render() {
    return (
      <Container>
        <Row className="justify-content-md-center pt-5">
          <Alert color="danger">This is a danger alert  check it out!</Alert>
          <Card border="default" style={{ width: "18rem" }}>
            <CardImg variant="top" src={logo} />
            <CardBody className="justify-content-center">
              <CardTitle className="text-center">
                Autenticaci&oacute;n
              </CardTitle>
              <Button className="btn-login" onClick={this.loginWithGoogle}>
                Login With Google
              </Button>
            </CardBody>
          </Card>
        </Row>
      </Container>
    );
  }
}

export default withSetting(LoginPage);
