import React, { Component } from "react";
import { ROUTES_CONSTANTS } from "../../../config/routes-constants";
import ProductManagementForm from "./product-management-form";
import history from "../../../config/history";
import { withSetting } from "../../../shared/context";
import moment from "moment";

class ProductManagementCreate extends Component {
  state = {
    productLines: [],
    brands: []
  };

  saveEntity = item => {
    const { firebase } = this.props.context;

    // Get a key for a new Post.
    var uid = firebase.products().push().key;
    item.created = moment.now();
    item.updated = moment.now();

    item.createdBy = this.props.context.auth.user.uid;

    firebase.product(uid).set(item, error => {
      if (error) {
        console.log(error);
      } else {
        history.push(ROUTES_CONSTANTS.PRODUCT.index());
      }
    });
  };

  async componentDidMount() {
    const productLines = await this.getProductLines();
    const brands = await this.getBrands();

    this.setState({ productLines, brands });
  }

  getProductLines = () => {
    return new Promise((resolve, reject) => {
      this.props.context.firebase.productLines().on(
        "value",
        snapshot => {
          const itemsObject = snapshot.val();
          const itemsList = itemsObject
            ? Object.keys(itemsObject).map(key => ({
                ...itemsObject[key],
                uid: key
              }))
            : [];
          resolve(itemsList);
        },
        error => {
          console.log(error);
          reject(error);
        }
      );
    });
  };

  getBrands = () => {
    return new Promise((resolve, reject) => {
      this.props.context.firebase.brands().on(
        "value",
        snapshot => {
          const itemsObject = snapshot.val();
          const itemsList = itemsObject
            ? Object.keys(itemsObject).map(key => ({
                ...itemsObject[key],
                uid: key
              }))
            : [];
          // console.log(itemsList);
          resolve(itemsList);
        },
        error => {
          console.log(error);
          reject(error);
        }
      );
    });
  };

  render() {
    return (
      <ProductManagementForm
        title={"Crear Producto"}
        saveEntity={this.saveEntity}
        productLines={this.state.productLines}
        brands={this.state.brands}
      />
    );
  }
}

export default withSetting(ProductManagementCreate);
