import React, { Component } from "react";
import history from "../../../config/history";
import { ROUTES_CONSTANTS } from "../../../config/routes-constants";
import BrandManagementForm from "./brand-management-form";
import Loading from "../../layouts/loading";
import { withSetting } from "../../../shared/context";
import moment from "moment";

class BrandManagementEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      item: null
    };

    this.getItem = this.props.context.firebase.brand;
  }
  componentDidMount() {
    this.setState({ loading: true });
    let uid = this.props.match.params.id;
    this.getItem(uid)
      .once("value")
      .then(snapshot => {
        const item = snapshot.val();
        this.setState({
          item,
          loading: false
        });
      })
      .catch(error => {
        console.log(error);
      });
  }

  saveEntity = item => {
		const { firebase } = this.props.context;
		
    let uid = this.props.match.params.id;

    item.created = !item.created ? moment.now() : item.created;
    item.updated = moment.now();

    firebase.brand(uid).set(item, error => {
      if (error) {
        console.log(error);
      } else {
        history.push(ROUTES_CONSTANTS.BRAND.index());
      }
    });
  };

  render() {
    return this.state.loading ? (
      <Loading />
    ) : (
      <BrandManagementForm
        title={"Actualizar marca"}
        saveEntity={this.saveEntity}
        item={this.state.item}
      />
    );
  }
}

export default withSetting(BrandManagementEdit);
